import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { List, Avatar, Button, Pagination, Card, Empty, Typography, Modal, notification, Spin, Divider } from 'antd';
import { cleanObject, processTime } from '../../utils/helpers';
import Truncate from 'react-truncate';
import qs from 'qs';
import {
    isBrowser,
    isMobile
} from "react-device-detect";
import InfiniteScroll from "react-infinite-scroll-component";
// actions
import { getAllBookmarks, createBookmark, deleteBookmark } from '../../redux/actions/BookmarkActions';

const { confirm } = Modal;

class Bookmark extends Component {
    state = {
        isLoading: true
    }

    componentDidMount() {
        let query = qs.parse(this.props.location.search.slice(1));
        this.props.getAllBookmarks(query).then(() => {
            this.setState({ isLoading: false })
        })
    }

    // use for mobile only
    fetchMore() {
        this.props.getAllBookmarks({ page: this.props.pagination.current + 1 })
    }

    // use for desktop only
    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.location.search !== this.props.location.search) {
            var query = qs.parse(nextProps.location.search.slice(1));
            this.setState({ isLoading: true });
            this.props.getAllBookmarks(query).then(() => {
                setTimeout(() => {
                    this.setState({
                        isLoading: false
                    })
                }, 800)
            });
        }
    }

    onChangeFilter(name, value) {
        this.setState({ isLoading: true });
        let query = qs.parse(this.props.location.search.slice(1));
        query = {
            ...query,
            [name]: value
        }
        cleanObject(query);

        this.props.history.push({
            pathname: '/bookmarks',
            search: qs.stringify(query)
        });
    }

    onDelete(id) {
        confirm({
            title: 'Cnh bo',
            content: 'Bn chc chn mun xo mc  lu ch?',
            cancelText: 'Hu',
            okText: 'OK',
            onOk: () => {
                this.props.deleteBookmark(id).then(() => {
                    notification.success({
                        message: ' xo thnh cng'
                    })
                }).catch(err => {
                    notification.error({
                        message: ' c li xy ra',
                        description: 'Vui lng th li'
                    })
                })
            }
        })
    }

    render() {
        var { user, bookmarks, pagination } = this.props;

        return (
            <div>
                {
                    bookmarks.length ? (
                        <Card>
                            <Typography.Title level={2}>Bi vit  lu</Typography.Title>
                            {
                                isBrowser ? (
                                    <div>
                                        <List
                                            itemLayout="horizontal"
                                            dataSource={bookmarks}
                                            renderItem={item => (
                                                <List.Item
                                                    actions={[
                                                        <Link to={`/posts/${item.post._id}`}>
                                                            <Button type="link" className="p-0">Xem bi vit</Button>
                                                        </Link>,
                                                        <Button onClick={() => this.onDelete(item._id)} type="link" danger className="p-0">Xo</Button>
                                                    ]}
                                                >
                                                    <List.Item.Meta
                                                        avatar={
                                                            <Avatar src={item.post.creator.avatar} />
                                                        }
                                                        title={
                                                            <Link className="text-none" to={`/users/${item.post.creator._id}`}>{item.post.creator.name}</Link>
                                                        }
                                                        description={
                                                            <React.Fragment>
                                                                <Truncate lines={2} ellipsis={<span>...</span>}>
                                                                    {item.post.content}
                                                                </Truncate>
                                                                <div><i>  lu vo lc {processTime(item.created_at)}</i></div>
                                                            </React.Fragment>
                                                        }
                                                    />
                                                </List.Item>
                                            )}
                                        />

                                        <div className="text-right mt-4">
                                            <Pagination
                                                {...pagination}
                                                pageSize={pagination.perPage}
                                                onChange={(page, pageSize) => this.onChangeFilter('page', page)}
                                            />
                                        </div>
                                    </div>
                                ) : null
                            }
                            {
                                isMobile ? (
                                    <InfiniteScroll
                                        className="notification-list"
                                        dataLength={pagination.perPage * pagination.current}
                                        next={() => this.fetchMore()}
                                        hasMore={pagination.current < pagination.totalPages}
                                        loader={<div className="text-center"><Spin size="large" /></div>}
                                    >
                                        {
                                            bookmarks.map(item => (
                                                <List.Item className="bookmark-mobile-items d-flex justify-content-start flex-column align-content-start" >
                                                    <List.Item.Meta
                                                        className="w-100"
                                                        avatar={
                                                            <Avatar src={item.post.creator.avatar} />
                                                        }
                                                        title={
                                                            <Link className="text-none" to={`/users/${item.post.creator._id}`}>{item.post.creator.name}</Link>
                                                        }
                                                        description={
                                                            <React.Fragment>
                                                                <Truncate lines={2} ellipsis={<span>...</span>}>
                                                                    {item.post.content}
                                                                </Truncate>
                                                                <div><small><i> lu vo lc {processTime(item.created_at)}</i></small></div>
                                                            </React.Fragment>
                                                        }
                                                    />
                                                    <div className="w-100">
                                                        <Link to={`/posts/${item.post._id}`}>
                                                            <Button type="link" className="p-0">Xem bi vit</Button>
                                                        </Link>
                                                        <Divider type="vertical" />
                                                        <Button onClick={() => this.onDelete(item._id)} type="link" danger className="p-0">Xo</Button>
                                                    </div>
                                                    
                                                </List.Item>
                                            ))
                                        }
                                    </InfiniteScroll>
                                ) : null
                            }
                        </Card>
                    ) : (
                            <Card>
                                <Typography.Title level={2}>Bi vit  lu</Typography.Title>
                                <Empty description="Khng c d liu" />
                            </Card>
                        )
                }
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser,
        bookmarks: state.bookmark.bookmarks,
        pagination: state.bookmark.pagination,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getAllBookmarks: (query) => dispatch(getAllBookmarks(query)),
        createBookmark: (data) => dispatch(createBookmark(data)),
        deleteBookmark: (id) => dispatch(deleteBookmark(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Bookmark);