import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { List, Avatar, Card, Empty, Typography, Modal, Button, Spin, Tooltip, Badge } from 'antd';
import { cleanObject, processTime } from '../../utils/helpers';
import {
    LikeOutlined,
    DislikeOutlined,
    MessageOutlined,
    SisternodeOutlined
} from '@ant-design/icons'
import qs from 'qs';
import {
    isBrowser,
    isMobile
} from "react-device-detect";
import InfiniteScroll from "react-infinite-scroll-component";
// actions
import { getAllNotifications, countUnread, markAllAsRead, markNotificationAsRead } from '../../redux/actions/NotificationActions';

const { confirm } = Modal;

class Notification extends Component {
    state = {
        isLoading: true
    }

    componentDidMount() {
        this.props.getAllNotifications().then(() => {
            this.setState({ isLoading: false })
        })
    }

    fetchMoreNotifications() {
        this.props.getAllNotifications({ page: this.props.pagination.current + 1 });
    }

    render() {
        var { user, notifications, pagination, unread } = this.props;

        return (
            <div>
                {
                    notifications.length ? (
                        <Card>
                            <Typography.Title level={2}>Thng bo {unread ? `(${unread})` : null}</Typography.Title>
                            <Button type="link" className="p-0" onClick={() => this.props.markAllAsRead()}>nh du tt c  c</Button>
                            {
                                isMobile ? (
                                    <InfiniteScroll
                                        dataLength={pagination.perPage * pagination.current}
                                        next={() => this.fetchMoreNotifications()}
                                        hasMore={pagination.current < pagination.totalPages}
                                        loader={<div className="text-center"><Spin size="large" /></div>}
                                    >
                                        {
                                            notifications.map(item => {
                                                let content = '';
                                                let icon = null;
                                                if (item.type == "like") {
                                                    icon = <LikeOutlined />;
                                                    if (item.comment) content = `${item.creator.name}  thch bnh lun ca bn`;
                                                    else content = `${item.creator.name}  thch bi vit ca bn`;
                                                } else if (item.type == 'dislike') {
                                                    icon = <DislikeOutlined />;
                                                    if (item.comment) content = `${item.creator.name} khng thch bnh lun ca bn`;
                                                    else content = `${item.creator.name} khng thch bi vit ca bn`;
                                                } else if (item.type == 'comment') {
                                                    icon = <MessageOutlined />;
                                                    content = `${item.creator.name}  bnh lun v bi vit bn ang theo di`;
                                                } else if (item.type == 'reply') {
                                                    icon = <SisternodeOutlined />;
                                                    content = `${item.creator.name}  tr li bnh lun m bn ang tham gia hoc theo di`;
                                                }

                                                return (
                                                    <List.Item key={item._id} className={`d-flex justify-content-between align-items-center notification-items`}>
                                                        <List.Item.Meta
                                                            avatar={<Avatar src={item.creator.avatar} />}
                                                            title={<Link className="text-none notification-content" to={`/posts/${item.post._id}`}>{content}</Link>}
                                                            description={<div>{icon} <small>{processTime(item.created_at)}</small></div>}
                                                        />
                                                        {item.is_read ? (
                                                            <Tooltip title=" c">
                                                                <Badge status="default" />
                                                            </Tooltip>
                                                        ) : (
                                                                <Tooltip title="nh du  c">
                                                                    <div onClick={() => this.props.markNotificationAsRead(item._id)}>
                                                                        <Badge color="#d0021a" />
                                                                    </div>
                                                                </Tooltip>
                                                            )
                                                        }
                                                    </List.Item>
                                                )
                                            })
                                        }
                                    </InfiniteScroll>
                                ) : (
                                    <Empty description="Ch xem c trn in thoi" />
                                )
                            }
                        </Card>
                    ) : (
                            <Card>
                                <Typography.Title level={2}>Bi vit  lu</Typography.Title>
                                <Empty description="Khng c d liu" />
                            </Card>
                        )
                }
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser,
        notifications: state.notification.notifications,
        pagination: state.notification.pagination,
        unread: state.notification.unread,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getAllNotifications: () => dispatch(getAllNotifications()),
        countUnread: () => dispatch(countUnread()),
        markAllAsRead: () => dispatch(markAllAsRead()),
        markNotificationAsRead: (id) => dispatch(markNotificationAsRead(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Notification);