import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Card, Spin, Skeleton, Row, Col, Alert } from 'antd';
import Post from '../../components/Post';
import PostForm from '../../components/Form/PostForm';
import InfiniteScroll from "react-infinite-scroll-component";
import MetaTags from '../../components/MetaTags';
import qs from 'qs';
import { cleanObject } from '../../utils/helpers';
import RightSidebar from '../../components/RightSidebar';
// actions
import { getAllPosts } from '../../redux/actions/PostActions';


class Home extends Component {
    state = {
        isLoading: true,
    }

    componentDidMount() {
        let query = qs.parse(this.props.location.search.slice(1));
        query = cleanObject(query);

        this.props.getAllPosts(query).then(() => {
            this.setState({ isLoading: false })
        })
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        console.log(nextProps.location.search, this.props.location.search)
        if (nextProps.location.search !== this.props.location.search) {

            let query = qs.parse(nextProps.location.search.slice(1));
            query = cleanObject(query);

            this.props.getAllPosts(query).then(() => {
                this.setState({ isLoading: false })
            })
        }
    }

    fetchMorePosts() {
        let query = qs.parse(this.props.location.search.slice(1));
        query = cleanObject(query);

        this.props.getAllPosts({ ...query, page: this.props.pagination.current + 1 });
    }

    render() {
        var { isLoading } = this.state;
        var { user, posts, pagination } = this.props;

        return (
            <React.Fragment>
                <MetaTags
                    title="BK Gallery"
                    ogTitle="BK Gallery | Cng ng chia s ti liu hc tp sinh vin Bch Khoa H Ni"
                />

                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>

                    <Col xs={24} sm={24} md={24} lg={15} xl={15}>
                        {
                            user ? <PostForm /> : null
                        }
                        {
                            isLoading ? (
                                <React.Fragment>
                                    {
                                        [1, 2, 3, 4, 5].map(item => (
                                            <Card
                                                key={item}
                                                className="single-post"
                                            >
                                                <Skeleton avatar paragraph={{ rows: 4 }} />
                                            </Card>
                                        ))
                                    }
                                </React.Fragment>
                            ) : (
                                    <InfiniteScroll
                                        dataLength={pagination.perPage * pagination.current}
                                        next={() => this.fetchMorePosts()}
                                        hasMore={pagination.current < pagination.totalPages}
                                        loader={<div className="text-center"><Spin size="large" /></div>}
                                    >
                                        {
                                            posts.map((post, index) => {
                                                return (
                                                    <Post
                                                        post={post}
                                                        reaction={post.reaction}
                                                        likeNumber={post.likes}
                                                        dislikeNumber={post.dislikes}
                                                        downloadNumber={post.downloads}
                                                        key={post._id}
                                                    />
                                                )
                                            })
                                        }
                                    </InfiniteScroll>
                                )
                        }
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={9} xl={9}>
                        <Alert 
                            className="mb-4"
                            message="Lu " 
                            type="info" 
                            showIcon 
                            closable 
                            description="Kch c file ti ln ti a l 5MB"
                        />
                        <RightSidebar/>
                    </Col>
                </Row>
            </React.Fragment>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser,
        posts: state.post.posts,
        pagination: state.post.pagination,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getAllPosts: (query) => dispatch(getAllPosts(query))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);