import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Card, Skeleton, Row, Col, Carousel, Spin, Avatar, Typography, Button, Divider, Upload, Tooltip, notification } from 'antd';
import Post from '../../components/Post';
import InfiniteScroll from "react-infinite-scroll-component";
import {
    MessageOutlined,
    EditOutlined,
    ShareAltOutlined,
    LoadingOutlined
} from '@ant-design/icons';
import FbImageLibrary from 'react-fb-image-grid';
import moment from 'moment';
import UserForm from './UserForm';
import MetaTags from '../../components/MetaTags';
import { FacebookShareButton } from "react-share";
import appConfig from '../../config';
import PostForm from '../../components/Form/PostForm';
import { uploadImage } from '../../utils/imgur';
import RightSidebar from '../../components/RightSidebar';
// actions
import { getUserDetail, updateUserDetail } from '../../redux/actions/UserActions';
import { getAllPosts } from '../../redux/actions/PostActions';

class User extends Component {
    uploadRef = React.createRef();

    state = {
        isLoading: true,
        isUpdateAvatarLoading: false,
        isOpenUpdateForm: false,
    }

    async componentDidMount() {
        await this.props.getUserDetail(this.props.match.params.id);
        await this.props.getAllPosts({ creator: this.props.match.params.id });

        this.setState({ isLoading: false })
    }

    async UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.match.params.id != this.props.match.params.id) {
            this.setState({ isLoading: true })

            await this.props.getUserDetail(nextProps.match.params.id);
            await this.props.getAllPosts({ creator: nextProps.match.params.id });

            this.setState({ isLoading: false })
        }
    }

    fetchMorePosts() {
        this.props.getAllPosts({ page: this.props.pagination.current + 1 });
    }

    toggleUpdateForm(visible) {
        this.setState({ isOpenUpdateForm: visible })
    }

    openChangeAvatar() {
        this.uploadRef.current.click()
    }

    async onChangeAvatar(file) {
        this.setState({ isUpdateAvatarLoading: true })
        if (file.status !== 'uploading') {
            var result = await uploadImage(file);

            this.props.updateUserDetail(this.props.authUser._id, { avatar: result }).then(() => {
                this.setState({ isUpdateAvatarLoading: false })
                notification.success({
                    message: ' cp nht avatar thnh cng'
                })
            }).catch(() => {
                this.setState({ isUpdateAvatarLoading: false })
                notification.error({
                    message: 'Li',
                    description: " c li xy ra. Vui lng th li,"
                })
            })
        }
    }

    render() {
        var { isLoading, isUpdateAvatarLoading, isOpenUpdateForm } = this.state;
        var { match, user, authUser, posts, pagination } = this.props;

        if (isLoading) {
            return (
                <div className="text-center">
                    <Spin size="large" />
                </div>
            )
        }

        var isMyProfile = false; // authUser == currentUser => isMyProfile = true
        if (authUser && match.params.id == authUser._id.toString()) isMyProfile = true;

        return (
            <React.Fragment>
                <MetaTags
                    title={user ? user.name : "BK Gallery"}
                    ogTitle={user ? `${user.name} trn BK Gallery` : "BK Gallery | Cng ng chia s ti liu hc tp sinh vin Bch Khoa H Ni"}
                />
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col xs={24} sm={24} md={24} lg={15} xl={15}>
                        <div className="user-header-wrapper">
                            <Carousel autoplay>
                                {
                                    [1, 2, 3, 4, 5].map(item => (
                                        <div key={item}>
                                            <img src={require(`../../assets/img/cover_${item}.jpg`)} className="cover-img" />
                                        </div>
                                    ))
                                }
                            </Carousel>
                            {
                                isMyProfile ? (
                                    <Tooltip title="Cp nht nh i din">
                                        <Avatar src={user.avatar} size={120} className="user-avatar" onClick={() => this.openChangeAvatar()} />
                                    </Tooltip>
                                ) : (
                                        <Avatar src={user.avatar} size={120} className="user-avatar" />
                                    )
                            }
                            <Card className="user-info">
                                <Typography.Title level={3} >{user.name}</Typography.Title>
                                <div>
                                    <MessageOutlined /><span className="ml-3">{user.description ? user.description : "Cha c m t"}</span>
                                </div>
                                <div>
                                    {isUpdateAvatarLoading ? <Button type="link" className="p-0 mr-2"><LoadingOutlined /></Button> : null}
                                    {
                                        isMyProfile ? (
                                            <React.Fragment>
                                                <Button className="p-0" type="link" icon={<EditOutlined />} onClick={() => this.toggleUpdateForm(true)}>
                                                    Sa thng tin c nhn
                                                </Button>
                                                <Divider type="vertical" />
                                            </React.Fragment>
                                        ) : null
                                    }
                                    <FacebookShareButton url={`${appConfig.ROOT_URL}/users/${user._id}`}>
                                        <Button className="p-0" type="link" icon={<ShareAltOutlined />}>
                                            Chia s
                                        </Button>
                                    </FacebookShareButton>
                                </div>
                            </Card>
                        </div>
                        <div className="user-posts">
                            {
                                isMyProfile ? <PostForm /> : null
                            }
                            <InfiniteScroll
                                dataLength={pagination.perPage * pagination.current}
                                next={() => this.fetchMorePosts()}
                                hasMore={pagination.current < pagination.totalPages}
                                loader={<div className="text-center"><Spin size="large" /></div>}
                            >
                                {
                                    posts.map((post, index) => {
                                        return (
                                            <Post
                                                post={post}
                                                reaction={post.reaction}
                                                likeNumber={post.likes}
                                                dislikeNumber={post.dislikes}
                                                downloadNumber={post.downloads}
                                                key={post._id}
                                            />
                                        )
                                    })
                                }
                            </InfiniteScroll>
                        </div>
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={9} xl={9}>
                        <Card className="user-images-card mb-4">
                            <div className="user-images-title">
                                <Typography.Title level={4} >Hnh nh ca {user.name}</Typography.Title>
                            </div>
                            <div className="post-images">
                                <FbImageLibrary
                                    images={user.images}
                                    countFrom={4}
                                    renderOverlay={() => "Xem nh"}
                                />
                            </div>
                        </Card>
                        <RightSidebar />
                    </Col>
                    <UserForm
                        visible={isOpenUpdateForm}
                        onCancel={() => this.toggleUpdateForm(false)}
                        user={user}
                    />
                </Row>
                <Upload
                    accept="image/*"
                    multiple={false}
                    beforeUpload={file => {
                        return false;
                    }}
                    listType="picture"
                    fileList={null}
                    onChange={({ file, fileList }) => this.onChangeAvatar(file)}
                >
                    <div ref={this.uploadRef}></div>
                </Upload>
            </React.Fragment>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.currentUser,
        authUser: state.user.authUser,
        posts: state.post.posts,
        pagination: state.post.pagination,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getUserDetail: (id) => dispatch(getUserDetail(id)),
        updateUserDetail: (id, data) => dispatch(updateUserDetail(id, data)),
        getAllPosts: (query) => dispatch(getAllPosts(query))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(User);