import api from '../../utils/api';
import {
    SIGN_IN,
    GET_USER_DETAIL,
    GET_AUTH_USER,
    UPDATE_USER_DETAIL,
    LOGOUT_USER
} from './types';
import { setCookie, removeCookie, getCookie } from '../../utils/session';

export const signIn = (data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.post('/users/signin', data).then(res => {
            setCookie('bkgallery_token', res.data.token, 30);

            dispatch({ type: SIGN_IN, payload: res.data.user });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const getAuthUser = (data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get('/users/auth').then(res => {
            dispatch({ type: GET_AUTH_USER, payload: res.data.user });
            resolve(true)
        }).catch(err => {
            console.log(err.response);
            reject(err);
        })
    })
}

export const getUserDetail = (id) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get(`/users/${id}`).then(res => {
            dispatch({ type: GET_USER_DETAIL, payload: res.data.user });
            resolve(true)
        }).catch(err => {
            console.log(err.response);
            reject(err);
        })
    })
}

export const updateUserDetail = (id, data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.put(`/users/${id}`, data).then(res => {
            console.log(res.data.user)
            dispatch({ type: UPDATE_USER_DETAIL, payload: res.data.user });
            resolve(true)
        }).catch(err => {
            console.log(err.response);
            reject(err);
        })
    })
}

export const logout = () => dispatch => {
    return new Promise((resolve, reject) => {
       removeCookie('bkgallery_token');
       dispatch({ type: LOGOUT_USER });
       resolve(true);
    })
 }
