import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import { Layout, List, Avatar, Divider, Typography, Card } from 'antd';
import {
    AppstoreTwoTone,
    HeartTwoTone,
    SnippetsTwoTone,
    BookTwoTone
} from '@ant-design/icons';
import FacebookLogin from 'react-facebook-login';
import GoogleLogin from 'react-google-login';
// actions
import { signIn } from '../redux/actions/UserActions';

const { Sider } = Layout;

class AppSidebar extends Component {
    state = {
        isAuthenticated: false
    }

    responseFacebook = (response) => {
        console.log(response);
    }

    responseGoogle = (response) => {
        var data = {
            auth_id: response.profileObj.googleId,
            avatar: response.profileObj.imageUrl,
            email: response.profileObj.email,
            name: response.profileObj.name,
            login_type: "google"
        }

        this.props.signIn(data);
    }

    render() {
        var { user } = this.props;

        var pathname = this.props.location.pathname;

        if (user) {
            return (
                <Sider theme="light" id="custom-sidebar">
                    <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/users/${user._id}` ? 'sidebar-items-active' : ''}`}>
                        <Link className="text-none" to={`/users/${user._id}`}>
                            <Avatar src={user.avatar} size={20} />
                            <Typography.Text className="mb-0 ml-2">{user.name}</Typography.Text>
                        </Link>
                    </div>
                    <Divider className="sidebar-divider" />
                    <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/` ? 'sidebar-items-active' : ''}`}>
                        <Link to='/' className="text-none">
                            <AppstoreTwoTone twoToneColor="#2ecc71" className="sidebar-icon" />
                            <Typography.Text className="mb-0 ml-2">Trang ch</Typography.Text>
                        </Link>
                    </div>
                    <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/history/liked` ? 'sidebar-items-active' : ''}`} >
                        <Link to='/history/liked' className="text-none">
                            <HeartTwoTone twoToneColor="#dd1818" className="sidebar-icon" />
                            <Typography.Text className="mb-0 ml-2"> thch</Typography.Text>
                        </Link>
                    </div>
                    <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/history/downloaded` ? 'sidebar-items-active' : ''}`}>
                        <Link to='/history/downloaded' className="text-none">
                            <SnippetsTwoTone twoToneColor="#d35400" className="sidebar-icon" />
                            <Typography.Text className="mb-0 ml-2">Lch s ti v</Typography.Text>
                        </Link>
                    </div>
                    <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/bookmarks` ? 'sidebar-items-active' : ''}`}>
                        <Link to='/bookmarks' className="text-none">
                            <BookTwoTone twoToneColor="#3498db" className="sidebar-icon" />
                            <Typography.Text className="mb-0 ml-2">Lu tr</Typography.Text>
                        </Link>
                    </div>
                </Sider>
            )
        } else {
            return (
                <Sider theme="light" id="custom-sidebar">
                    <Card>
                        <Typography.Title level={3}>Xin cho!</Typography.Title>
                        <div>
                            Bn ang n vi BK Gallery, cng ng chia s ti liu dnh cho sinh vin Bch Khoa H Ni.
                            Hy ng nhp  c th xem, th cm xc bi vit, ti ti liu v hn th na!
                        </div>
                        <Divider>ng nhp vi</Divider>
                        <div className="text-center">
                            <FacebookLogin
                                appId="1951615681826197"
                                autoLoad={true}
                                fields="name,email,picture"
                                callback={this.responseFacebook}
                                textButton="ng nhp"
                                icon="fa-facebook"
                            />
                            <div className="mt-1 mb-1">hoc</div>
                            <GoogleLogin
                                clientId="857631529122-33h9t3i8rgnnk2bghnvh8hr4fm088fp3.apps.googleusercontent.com"
                                buttonText="ng nhp"
                                onSuccess={this.responseGoogle}
                                onFailure={this.responseGoogle}
                                cookiePolicy={'single_host_origin'}
                            />
                        </div>
                    </Card>
                </Sider>
            )
        }
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser
    }
}

function mapDispatchToProps(dispatch) {
    return {
        signIn: (data) => dispatch(signIn(data))
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AppSidebar));