import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Comment, Tooltip, List, Button, Modal, Input, Form, Divider } from 'antd';
// actions
import { updateUserDetail } from '../../redux/actions/UserActions';

class UserForm extends Component {
    onSubmit = (values) => {
        this.props.updateUserDetail(this.props.user._id, values).then(() => {
            this.props.onCancel();
        })
    }

    render() {
        var { visible, user, onCancel } = this.props;

        return (
            <Modal
                visible={visible}
                title="Cp nht thng tin user"
                onCancel={() => onCancel()}
                footer={null}
            >
                <Form
                    layout="vertical"
                    initialValues={user}
                    onFinish={this.onSubmit}
                >
                    <Form.Item
                        name="name"
                        label="Tn ca bn"
                        rules={[
                            { required: true, message: "Vui lng in tn ca bn" }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name="description"
                        label="M t ngn v bn thn"
                        rules={[
                            { required: false, message: "Vui lng in m t" }
                        ]}
                    >
                        <Input.TextArea />
                    </Form.Item>
                    <Form.Item>
                        <Button type="default" onClick={() => onCancel()}>Hu</Button>
                        <Divider type="vertical" />
                        <Button type="primary" htmlType="submit">Cp nht</Button>
                    </Form.Item>
                </Form>
            </Modal>
        )
    }
}

function mapStateToProps(state) {
    return {
        // user: state.user.authUser
    }
}

function mapDispatchToProps(dispatch) {
    return {
        updateUserDetail: (id, data) => dispatch(updateUserDetail(id, data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(UserForm);