import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import { Layout, Modal, Drawer, Button, Avatar, Row, Col, Typography, Input, Divider, Popover, List, Spin, Badge, Tooltip } from 'antd';
import {
    MenuFoldOutlined,
    UserOutlined,
    BellOutlined,
    HeartTwoTone,
    SnippetsTwoTone,
    BookTwoTone,
    BellTwoTone,
    SmileOutlined,
    LogoutOutlined,
    LikeOutlined,
    DislikeOutlined,
    MessageOutlined,
    SisternodeOutlined,
    AppstoreTwoTone
} from '@ant-design/icons';
import qs from 'qs';
import { cleanObject, processTime } from '../utils/helpers';
import InfiniteScroll from "react-infinite-scroll-component";
// actions
import { getAllNotifications, countUnread, markAllAsRead, markNotificationAsRead } from '../redux/actions/NotificationActions';
import { logout } from '../redux/actions/UserActions';

const { Header } = Layout;
const { Search } = Input;
const { confirm } = Modal;

const pusher = new window.Pusher('f3504a17049e9513ffc4', {
    cluster: 'ap1'
});

class AppHeader extends Component {
    userActions = [
        {
            icon: <SmileOutlined />,
            name: "Trang c nhn",
            action: () => this.redirectToProfilePage()
        },
        {
            icon: <LogoutOutlined />,
            name: "ng xut",
            action: () => this.onLogout()
        },
    ]

    state = {
        visibleDrawer: false
    }

    async componentDidMount() {
        await this.props.getAllNotifications();
        await this.props.countUnread();
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.user && nextProps.user != this.props.user) {
            const component = this;
            var channel = pusher.subscribe(`bkgallery-${nextProps.user._id}`);
            channel.bind('new-notification', function (data) {
                component.props.getAllNotifications();
            });
        }
    }

    fetchMoreNotifications() {
        this.props.getAllNotifications({ page: this.props.pagination.current + 1 });
    }

    toggleDrawer(isOpen) {
        this.setState({ visibleDrawer: isOpen })
    }

    onSearch(value) {
        this.toggleDrawer(false);
        this.props.history.push({
            pathname: '/',
            search: qs.stringify({ keyword: value })
        });
    }

    redirectToProfilePage() {
        window.location.href = `/users/${this.props.user._id}`;
    }

    onLogout() {
        confirm({
            title: 'Cnh bo',
            content: 'Bn mun ng xut khi BK Gallery?',
            cancelText: 'Hu',
            okText: 'ng xut',
            onOk: () => this.props.logout()
        });

        this.toggleDrawer(false);
    }

    render() {
        var { user, notifications, pagination, unread } = this.props;
        var { visibleDrawer } = this.state;

        let query = qs.parse(this.props.location.search.slice(1));
        query = cleanObject(query);

        var pathname = this.props.location.pathname;

        return (
            <Header id="custom-header" style={{ backgroundColor: '#d0021a' }}>
                <Row justify="space-between">
                    <Col className="d-flex justify-content-between align-items-center">
                        <div className="logo">
                            <a href="/">
                                <Avatar shape="square" size={40} src={require('../assets/img/logo.png')} />
                            </a>
                        </div>
                        <Search
                            defaultValue={query.keyword || ''}
                            className="custom-searchbox ml-4"
                            placeholder="Tm kim..."
                            onSearch={value => this.onSearch(value)}
                        />
                    </Col>
                    {
                        user ? (
                            <Col id="custom-desktop-menu">
                                <Popover
                                    placement="bottomRight"
                                    content={
                                        <div>
                                            <InfiniteScroll
                                                className="notification-list"
                                                dataLength={pagination.perPage * pagination.current}
                                                next={() => this.fetchMoreNotifications()}
                                                hasMore={pagination.current < pagination.totalPages}
                                                loader={<div className="text-center"><Spin size="large" /></div>}
                                                height={350}
                                            >
                                                {
                                                    notifications.map(item => {
                                                        let content = '';
                                                        let icon = null;
                                                        if (item.type == "like") {
                                                            icon = <LikeOutlined />;
                                                            if (item.comment) content = `${item.creator.name}  thch bnh lun ca bn`;
                                                            else content = `${item.creator.name}  thch bi vit ca bn`;
                                                        } else if (item.type == 'dislike') {
                                                            icon = <DislikeOutlined />;
                                                            if (item.comment) content = `${item.creator.name} khng thch bnh lun ca bn`;
                                                            else content = `${item.creator.name} khng thch bi vit ca bn`;
                                                        } else if (item.type == 'comment') {
                                                            icon = <MessageOutlined />;
                                                            content = `${item.creator.name}  bnh lun v bi vit bn ang theo di`;
                                                        } else if (item.type == 'reply') {
                                                            icon = <SisternodeOutlined />;
                                                            content = `${item.creator.name}  tr li bnh lun m bn ang tham gia hoc theo di`;
                                                        }

                                                        return (
                                                            <List.Item key={item._id} className={`d-flex justify-content-between align-items-center notification-items`}>
                                                                <List.Item.Meta
                                                                    avatar={<Avatar src={item.creator.avatar} />}
                                                                    title={<Link className="text-none notification-content" to={`/posts/${item.post._id}`}>{content}</Link>}
                                                                    description={<div>{icon} <small>{processTime(item.created_at)}</small></div>}
                                                                />
                                                                {item.is_read ? (
                                                                    <Tooltip title=" c">
                                                                        <Badge status="default" />
                                                                    </Tooltip>
                                                                ) : (
                                                                        <Tooltip title="nh du  c">
                                                                            <div onClick={() => this.props.markNotificationAsRead(item._id)}>
                                                                                <Badge color="#d0021a" />
                                                                            </div>
                                                                        </Tooltip>
                                                                    )
                                                                }

                                                            </List.Item>
                                                        )
                                                    })
                                                }
                                            </InfiniteScroll>
                                            <div className="text-center"><Button type="link" className="p-0" onClick={() => this.props.markAllAsRead()}>nh du tt c  c</Button></div>
                                        </div>
                                    }
                                    title="Thng bo"
                                >
                                    <Button type="link" icon={<Badge count={unread}><BellOutlined className="header-icons" /></Badge>} />
                                </Popover>
                                <Popover
                                    placement="bottom"
                                    content={
                                        <List
                                            size="small"
                                            header={null}
                                            footer={null}
                                            bordered={false}
                                            dataSource={this.userActions}
                                            renderItem={item => (
                                                <List.Item className="item-action-btn" onClick={item.action}>{item.icon} {item.name}</List.Item>
                                            )}
                                        />
                                    }
                                    title={null}
                                >
                                    <Button type="link" icon={<UserOutlined className="header-icons" />} />
                                </Popover>
                            </Col>
                        ) : null
                    }
                    <Col id="custom-mobile-menu">
                        <Button type="link" icon={<MenuFoldOutlined style={{ color: '#fff' }} />} onClick={() => this.toggleDrawer(true)} />
                    </Col>
                </Row>

                <Drawer
                    placement="right"
                    closable={false}
                    onClose={() => this.toggleDrawer(false)}
                    visible={visibleDrawer}
                >
                    {
                        user ? (
                            <div className="d-flex justify-content-start align-items-center sidebar-mobile-items">
                                <Avatar src={user.avatar} size={20} />
                                <Typography.Text className="mb-0 ml-2">{user.name}</Typography.Text>
                            </div>
                        ) : null
                    }

                    <Divider className="custom-sidebar-divider" />
                    <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/` ? 'sidebar-items-active' : ''}`}>
                        <Link to='/' className="text-none" onClick={() => this.toggleDrawer(false)}>
                            <AppstoreTwoTone twoToneColor="#2ecc71" className="sidebar-icon" />
                            <Typography.Text className="mb-0 ml-2">Trang ch</Typography.Text>
                        </Link>
                    </div>
                    {
                        user ? (
                            <React.Fragment>
                                <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/notifications` ? 'sidebar-items-active' : ''}`} >
                                    <Link to='/notifications' className="text-none" onClick={() => this.toggleDrawer(false)}>
                                        <BellTwoTone twoToneColor="#f39c12" className="sidebar-icon" />
                                        <Typography.Text className="mb-0 ml-2">Thng bo</Typography.Text>
                                    </Link>
                                </div>
                                <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/history/liked` ? 'sidebar-items-active' : ''}`} >
                                    <Link to='/history/liked' className="text-none" onClick={() => this.toggleDrawer(false)}>
                                        <HeartTwoTone twoToneColor="#dd1818" className="sidebar-icon" />
                                        <Typography.Text className="mb-0 ml-2"> thch</Typography.Text>
                                    </Link>
                                </div>
                                <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/history/downloaded` ? 'sidebar-items-active' : ''}`}>
                                    <Link to='/history/downloaded' className="text-none" onClick={() => this.toggleDrawer(false)}>
                                        <SnippetsTwoTone twoToneColor="#d35400" className="sidebar-icon" />
                                        <Typography.Text className="mb-0 ml-2">Lch s ti v</Typography.Text>
                                    </Link>
                                </div>
                                <div className={`d-flex justify-content-start align-items-center sidebar-items ${pathname == `/bookmarks` ? 'sidebar-items-active' : ''}`}>
                                    <Link to='/bookmarks' className="text-none" onClick={() => this.toggleDrawer(false)}>
                                        <BookTwoTone twoToneColor="#3498db" className="sidebar-icon" />
                                        <Typography.Text className="mb-0 ml-2">Lu tr</Typography.Text>
                                    </Link>
                                </div>
                                <div className={`d-flex justify-content-start align-items-center sidebar-items`}>
                                    <div onClick={() => this.onLogout()}>
                                        <LogoutOutlined className="sidebar-icon" />
                                        <Typography.Text className="mb-0 ml-2">ng xut</Typography.Text>
                                    </div>
                                </div>
                            </React.Fragment>
                        ) : null
                    }
                    <Divider className="custom-sidebar-divider" />
                    <Search
                        defaultValue={query.keyword || ''}
                        placeholder="Tm kim..."
                        onSearch={value => this.onSearch(value)}
                    />
                </Drawer>
            </Header>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser,
        notifications: state.notification.notifications,
        pagination: state.notification.pagination,
        unread: state.notification.unread,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        logout: () => dispatch(logout()),
        getAllNotifications: () => dispatch(getAllNotifications()),
        countUnread: () => dispatch(countUnread()),
        markAllAsRead: () => dispatch(markAllAsRead()),
        markNotificationAsRead: (id) => dispatch(markNotificationAsRead(id))
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AppHeader));