import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Card, Divider, Button, Upload, notification, Modal } from 'antd';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import 'react-quill/dist/quill.bubble.css';
import {
    PictureTwoTone,
    FileTwoTone
} from '@ant-design/icons';
import { uploadImage } from '../../utils/imgur';
// actions
import { createPost } from '../../redux/actions/PostActions';


class PostForm extends Component {
    state = {
        isLoading: false,
        isFocused: false,
        value: "",
        fileList: [],
        imageList: [],
        postType: 'normal' // 3 types: normal, image, file
    }

    onChange(value) {
        this.setState({ value })
    }

    onFocusEditor(range, source, editor) {
        this.setState({ isFocused: true })
    }

    onBlurEditor(range, source, editor) {
        this.setState({ isFocused: false })
    }

    onChangeImage = ({ file, fileList }) => {
        if (file.status !== 'uploading') {
            this.setState({
                imageList: fileList,
                fileList: [],
                postType: fileList.length ? 'image' : 'normal'
            })
        }
    }

    onChangeFile = ({ file, fileList }) => {
        if(file.size >= 5000000) {
            notification.warning({
                message: 'Cnh bo',
                description: 'Khng th upload file c dung lng ln hn 5MB'
            });

            this.setState({
                imageList: [],
                fileList: [],
                postType: 'normal'
            })
        } else {
            if (file.status !== 'uploading') {
                this.setState({
                    imageList: [],
                    fileList: fileList,
                    postType: fileList.length ? 'file' : 'normal'
                })
            }
        }
    }

    async onSubmit(e) {
        e.preventDefault();
        this.setState({isLoading: true});

        var { fileList, imageList, value } = this.state;

        if (!value) {
            notification.error({
                message: 'Li',
                description: 'Bn cha thm ni dung bi vit'
            });
        } else {
            var submitData = new FormData();
            submitData.set('content', value);
    
            if (fileList.length) {
                let file = fileList[0].originFileObj;
    
                submitData.append('file', file);
                submitData.set('fileName', file.name);
                submitData.set('fileType', file.type);
            }
    
            if (imageList.length) {
                var requests = imageList.map(item => {
                    return uploadImage(item.originFileObj)
                });
    
                var images = await Promise.all(requests);
                images = images.filter(item => item); // filter null image
    
                submitData.set('images', JSON.stringify(images));
            }
    
            this.props.createPost(submitData).then(() => {
                this.setState({
                    imageList: [],
                    fileList: [],
                    postType: 'normal',
                    isFocused: false,
                    value: "",
                    isLoading: false
                })
            }).catch(err => {
                notification.error({
                    message: 'Li',
                    description: err.response.data.message
                });
                this.setState({
                    isLoading: false
                })
            })
        }
    }

    render() {
        var { value, isFocused, fileList, imageList, postType, isLoading } = this.state;

        var modules = {
            toolbar: [
                [{ 'header': [1, 2, 3, 4, false] }],
                ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                [{ 'list': 'ordered' }, { 'list': 'bullet' }],
                ['link']
            ],
        }

        var formats = [
            'header',
            // {
            //     name: "h1",
            //     definition: { tag: "H1", prepare: "heading", type: "line" }
            // },
            // {
            //     name: "h2",
            //     definition: { tag: "H2", prepare: "heading", type: "line" }
            // },
            // {
            //     name: "h3",
            //     definition: { tag: "H3", prepare: "heading", type: "line" }
            // }
            'bold', 'italic', 'underline', 'strike', 'blockquote',
            'list', 'bullet',
            'link', 'image'
        ]

        return (
            <Card className="form-post mb-4">
                <ReactQuill
                    theme={isFocused ? "snow" : "bubble"}
                    placeholder="Bn mun chia s iu g?"
                    value={value}
                    onChange={(value) => this.onChange(value)}
                    modules={modules}
                    formats={formats}
                    onFocus={(range, source, editor) => this.onFocusEditor(range, source, editor)}
                // onBlur={(range, source, editor) => this.onBlurEditor(range, source, editor)}
                />
                <Divider />
                <small><i>Lu : Bn ch c th upload nh hoc file. Khng th upload c hai cng lc.</i></small>
                <div className="d-flex justify-content-between align-items-start mt-2">
                    <div className="d-flex justify-content-start align-items-center" id="upload-buttons">
                        {
                            postType != 'file' ? (
                                <Upload
                                    onChange={this.onChangeImage}
                                    accept="image/*"
                                    multiple
                                    beforeUpload={file => {
                                        this.setState(state => ({
                                            imageList: [...state.imageList, file],
                                        }));
                                        return false;
                                    }}
                                    fileList={imageList}
                                    listType="picture"
                                >
                                    <Button shape="round" icon={<PictureTwoTone />}>nh</Button>
                                </Upload>
                            ) : null
                        }
                        {
                            postType != 'image' ? (
                                <Upload
                                    onChange={this.onChangeFile}
                                    beforeUpload={({ file }) => {
                                        this.setState({
                                            fileList: [file]
                                        })
                                        return false;
                                    }}
                                    fileList={fileList}
                                >
                                    <Button className="ml-2" shape="round" icon={<FileTwoTone />}>File</Button>
                                </Upload>
                            ) : null
                        }
                    </div>
                    <Button loading={isLoading} type="primary" shape="round" onClick={(e) => this.onSubmit(e)}>ng</Button>
                </div>
            </Card>
        )
    }
}

function mapStateToProps(state) {
    return {

    }
}

function mapDispatchToProps(dispatch) {
    return {
        createPost: (data) => dispatch(createPost(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PostForm);