import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Comment, Tooltip, List, Button, Modal, Input, Typography } from 'antd';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import 'react-quill/dist/quill.bubble.css';

// actions
import { getAllComments, createComment, reactComment } from '../../redux/actions/CommentActions';
import CommentItem from './CommentItem';

class CommentList extends Component {
    state = {
        isOpenCreateForm: false,
        value: '',
        comments: [],
        currentComment: null,
        isShowAllComments: false
    }

    componentDidMount() {
        this.props.getAllComments({ post: this.props.post }).then(data => {
            this.setState({ comments: data.comments })
        })
    }

    onToggleModal(visible, comment = null) {
        this.setState({ isOpenCreateForm: visible, currentComment: comment })
    }

    onChange(value) {
        this.setState({ value })
    }

    submit(e) {
        e.preventDefault();
        let { value, currentComment, comments } = this.state;

        this.props.createComment({
            post: this.props.post,
            content: value,
            parent_comment: currentComment
        }).then((data) => {
            if (currentComment) {
                let commentList = [...comments];
                let index = commentList.findIndex(item => item._id.toString() == currentComment.toString());

                commentList[index].child_comments = [
                    ...commentList[index].child_comments,
                    {
                        ...data.comment,
                        user: this.props.user,
                        child_comments: []
                    }
                ]

                this.setState({
                    value: '',
                    isOpenCreateForm: false,
                    comments: commentList,
                    currentComment: null
                })
            } else {
                this.setState({
                    value: '',
                    isOpenCreateForm: false,
                    comments: [
                        ...this.state.comments,
                        {
                            ...data.comment,
                            user: this.props.user,
                            child_comments: []
                        }
                    ],
                    currentComment: null
                })
            }
        })
    }

    onToggleShowComments() {
        this.setState({
            isShowAllComments: !this.state.isShowAllComments,
            collapseComment: true
        })
    }

    render() {
        var {user} = this.props;
        var { isOpenCreateForm, value, comments, isShowAllComments } = this.state;

        let displayedComments = isShowAllComments ? comments : comments.slice(Math.max(comments.length - 2, 0));

        return (
            <div className="comments">
                <div className="d-flex justify-content-between align-items-center">
                    {
                        user ? (
                            <Button type="link" className="p-0" onClick={() => this.onToggleModal(true)}>
                                Thm bnh lun...
                            </Button>
                        ) : (
                            <div></div>
                        )
                    }
                    {
                        comments.length ? (
                            <React.Fragment>
                                {
                                    comments.length > 2 ? (
                                        <div className="text-secondary toggle-comment-btn" onClick={() => this.onToggleShowComments()}>
                                            {isShowAllComments ? "Thu gn" : `Xem ${comments.length} bnh lun`}
                                        </div>
                                    ) : null
                                }
                            </React.Fragment>
                        ) : (
                                <div className="text-secondary">Cha c bnh lun no</div>
                            )
                    }
                </div>
                {
                    displayedComments.length ? (
                        <List
                            className={`comment-list`}
                            header={null}
                            itemLayout="horizontal"
                            dataSource={displayedComments}
                            renderItem={item => (
                                <CommentItem key={item._id} comment={item} onReply={() => this.onToggleModal(true, item._id)} isShowAllComments={isShowAllComments}>
                                    {
                                        item.child_comments.length ? (
                                            <List
                                                className={`comment-list`}
                                                header={null}
                                                itemLayout="horizontal"
                                                dataSource={item.child_comments}
                                                renderItem={child => (
                                                    <CommentItem comment={child} onReply={() => this.onToggleModal(true, item._id)} />
                                                )}
                                            />
                                        ) : null
                                    }
                                </CommentItem>
                            )}
                        />
                    ) : null
                }

                <Modal
                    visible={isOpenCreateForm}
                    title="Thm bnh lun"
                    cancelText="Hu"
                    okText="Thm"
                    onCancel={() => this.onToggleModal(false)}
                    onOk={(e) => this.submit(e)}
                >
                    <div className="form-post mb-4">
                        <ReactQuill
                            theme={"bubble"}
                            placeholder="Vit bnh lun ca bn  y..."
                            value={value}
                            onChange={(value) => this.onChange(value)}
                            modules={{}}
                            formats={[
                                'header',
                                'bold', 'italic', 'underline', 'strike', 'blockquote',
                                'list', 'bullet',
                                'link', 'image'
                            ]}
                        />
                    </div>
                </Modal>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser
    }
}

function mapDispatchToProps(dispatch) {
    return {
        createComment: (data) => dispatch(createComment(data)),
        getAllComments: (query) => dispatch(getAllComments(query)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CommentList);