import api from '../../utils/api';
import {
    CREATE_POST,
    GET_ALL_POSTS,
    REACTION_POST,
    GET_POST_DETAIL,
    DOWNLOAD_FILE,
    UPDATE_POST_CONTENT,
    BOOKMARK_POST,
    REPORT_POST,
    DELETE_POST,
} from './types';
import FileSaver from 'file-saver';

export const createPost = (data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.post('/posts', data).then(res => {
            dispatch({ type: CREATE_POST, payload: res.data.post });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const getAllPosts = (query) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get('/posts', {params: query}).then(res => {
            dispatch({ type: GET_ALL_POSTS, payload: res.data });
            resolve(res.data)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

