export const updatePostDetail = (id, data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.put(`/posts/${id}`, data).then(res => {
            dispatch({ type: UPDATE_POST_CONTENT, payload: res.data });
            resolve(res.data)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const downloadFile = (post) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.post('/posts/file', {post_id: post._id}, {responseType: 'blob',}).then(res => {
            console.log(res.headers)
            dispatch({ type: DOWNLOAD_FILE, payload: post._id });
            FileSaver.saveAs(res.data, post.file_name);
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

