import _ from 'lodash';
import {
    CREATE_BOOKMARK,
    GET_ALL_BOOKMARKS,
    GET_ALL_BOOKMARKS_WITHOUT_PAGINATION,
    DELETE_BOOKMARK
} from '../actions/types';
import {
    isBrowser,
    isMobile
} from "react-device-detect";

const INIT_STATE = {
    bookmarks: [],
    pagination: {
        current: 1,
        total: 0,
        totalPages: 0,
        perPage: 0
    }
};

export default (state = INIT_STATE, action) => {
    switch (action.type) {
        case CREATE_BOOKMARK: {
            let bookmarks = [...state.bookmarks];
            
            bookmarks = [
                action.payload.record,
                ...bookmarks
            ]

            return {
                ...state,
                bookmarks: bookmarks
            };
        }
        case GET_ALL_BOOKMARKS: {
            let bookmarks = [...action.payload.records];

            if(isMobile) {
                if(action.payload.current > 1) bookmarks = [...state.bookmarks, ...action.payload.records]
            }
            
            return {
                ...state,
                bookmarks: bookmarks,
                pagination: {
                    current: action.payload.current,
                    total: action.payload.total,
                    totalPages: action.payload.pages,
                    perPage: action.payload.perPage
                }
            };
        }
        case DELETE_BOOKMARK: {
            let currentBookmarks = [...state.bookmarks];

            let bookmarks = currentBookmarks.filter((item) => {
                return item._id.toString() != action.payload.toString();
            })

            return { ...state, bookmarks: bookmarks };
        }
        default: {
            return { ...state };
        }
    }
}