import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Card, Skeleton, Row, Col, Empty } from 'antd';
import Post from '../../components/Post';
import MetaTags from '../../components/MetaTags';
import RightSidebar from '../../components/RightSidebar';
// actions
import { getPostDetail } from '../../redux/actions/PostActions';

class PostDetail extends Component {
    state = {
        isLoading: true,
    }

    componentDidMount() {
        this.props.getPostDetail(this.props.match.params.id).then(() => {
            this.setState({ isLoading: false })
        })
    }

    async UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.match.params.id != this.props.match.params.id) {
            this.setState({ isLoading: true })

            await this.props.getPostDetail(nextProps.match.params.id);

            this.setState({ isLoading: false })
        }
    }

    render() {
        var { isLoading } = this.state;
        var { post } = this.props;

        return (
            <React.Fragment>
                <MetaTags
                    title={post ? `Bi vit ca ${post.creator.name}` : "Bi vit trn BK Gallery"}
                    ogTitle={post ? 
                        `BK Gallery - Bi vit ca ${post.creator.name} | Cng ng chia s ti liu hc tp sinh vin Bch Khoa H Ni` 
                        : 'BK Gallery - Bi vit | Cng ng chia s ti liu hc tp sinh vin Bch Khoa H Ni'
                    }
                />

                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col xs={24} sm={24} md={24} lg={15} xl={15}>
                        {
                            isLoading ? (
                                <Card className="single-post">
                                    <Skeleton avatar paragraph={{ rows: 4 }} />
                                </Card>
                            ) : (
                                <React.Fragment>
                                    {
                                        post ? (
                                            <Post
                                                post={post}
                                                reaction={post.reaction}
                                                likeNumber={post.likes}
                                                dislikeNumber={post.dislikes}
                                                downloadNumber={post.downloads}
                                            />
                                        ) : (
                                            <Card className="single-post">
                                                <Empty description="Khng tm thy bi vit" />
                                            </Card>
                                        )
                                    }
                                </React.Fragment>
                                )
                        }
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={9} xl={9}>
                        <RightSidebar />
                    </Col>
                </Row>
            </React.Fragment>
        )
    }
}

function mapStateToProps(state) {
    return {
        post: state.post.currentPost
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getPostDetail: (id) => dispatch(getPostDetail(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PostDetail);