import api from '../../utils/api';
import {
    GET_ALL_NOTIFICATIONS,
    MARK_ALL_AS_READ,
    MARK_NOTIFICATION_AS_READ,
    COUNT_UNREAD
} from './types';

export const getAllNotifications = (query) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get('/notifications', {params: query}).then(res => {
            dispatch({ type: GET_ALL_NOTIFICATIONS, payload: res.data });
            resolve(res.data)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const markNotificationAsRead = (id) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.put(`/notifications/${id}`).then(res => {
            dispatch({ type: MARK_NOTIFICATION_AS_READ, payload: id });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const markAllAsRead = (id) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.put(`/notifications`).then(res => {
            dispatch({ type: MARK_ALL_AS_READ });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const countUnread = (id) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get(`/notifications/unread`).then(res => {
            dispatch({ type: COUNT_UNREAD, payload: res.data.total });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}