import {
    SIGN_IN,
    GET_AUTH_USER,
    GET_USER_DETAIL,
    UPDATE_USER_DETAIL,
    LOGOUT_USER
} from '../actions/types';

const INIT_STATE = {
    authUser: null,
    currentUser: null
};

export default (state = INIT_STATE, action) => {
    switch (action.type) {
        case SIGN_IN: {
            return { ...state, authUser: action.payload };
        }
        case GET_AUTH_USER: {
            return { ...state, authUser: action.payload };
        }
        case GET_USER_DETAIL: {
            return { ...state, currentUser: action.payload };
        }
        case UPDATE_USER_DETAIL: {
            let updatedUser = action.payload;
            let user = Object.assign({}, state.authUser);
            user = { ...user, ...updatedUser }

            if (state.currentUser && state.authUser._id.toString() == state.currentUser._id.toString()) {
                let current = Object.assign({}, state.currentUser);
                
                current = { ...current, ...updatedUser}

                return { ...state, authUser: user, currentUser: current };
            } else {
                return { ...state, authUser: user };
            }


        }
        case LOGOUT_USER: {
            return { ...state, authUser: null };
        }
        default: {
            return { ...state };
        }
    }
}