import api from '../../utils/api';
import {
    CREATE_COMMENT,
    GET_ALL_COMMENTS,
    REACTION_COMMENT
} from './types';

export const createComment = (data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.post('/comments', data).then(res => {
            dispatch({ type: CREATE_COMMENT, payload: res.data });
            resolve(res.data)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const getAllComments = (query) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get('/comments', {params: query}).then(res => {
            dispatch({ type: GET_ALL_COMMENTS, payload: res.data });
            resolve(res.data)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const reactComment = (data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.post('/comments/reaction', data).then(res => {
            dispatch({ type: REACTION_COMMENT, payload: res.data });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}