import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Card, Avatar, Typography, Divider, Modal, notification, Popover, List, Radio } from 'antd';
import {
    MessageOutlined,
    ShareAltOutlined,
    CloudDownloadOutlined,
    FileProtectOutlined,
    LikeTwoTone,
    DislikeTwoTone,
    ClockCircleOutlined,
    LoadingOutlined,
    EllipsisOutlined,
    FormOutlined,
    BookOutlined,
    StopOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import ReactionIcon from '../../components/ReactionIcon';
import ReactHtmlParser from 'react-html-parser';
import FbImageLibrary from 'react-fb-image-grid';
import { processTime } from '../../utils/helpers';
import { FacebookShareButton } from "react-share";
import CommentList from '../Comment';
import appConfig from '../../config';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import 'react-quill/dist/quill.bubble.css';
// actions
import { reactPost, downloadFile, updatePostDetail, reportPost, deletePost } from '../../redux/actions/PostActions';
import { createBookmark } from '../../redux/actions/BookmarkActions';

const { Meta } = Card;
const { confirm } = Modal;

const radioStyle = {
    display: 'block',
    height: '30px',
    lineHeight: '30px',
};

class Post extends Component {
    postMoreActions = [
        {
            icon: <BookOutlined />,
            name: "Lu bi vit",
            action: () => this.onBookmark()
        },
        {
            icon: <StopOutlined />,
            name: "Bo co bi vit ny",
            action: () => this.toggleReportModal(true)
        }
    ]

    state = {
        isShowCommentList: true,
        isLoadingDownload: false,
        isOpenReportModal: false,
        isOpenUpdateModal: false,
        reportValue: null,
        postContent: ''
    }

    componentDidMount() {
        this.setState({
            postContent: this.props.post.content
        })

        if (this.props.user && this.props.post.creator._id.toString() == this.props.user._id.toString()) {
            this.postMoreActions = [
                {
                    icon: <FormOutlined />,
                    name: "Sa ni dung bi vit",
                    action: () => this.toggleUpdateModal(true)
                },
                ...this.postMoreActions,
                {
                    icon: <DeleteOutlined />,
                    name: "Xo bi vit ny",
                    action: () => this.onDelete(true)
                }
            ]
        }
    }

    /**
     * 
     * @param {*} type like or dislike
     * @param {*} isRemove remove current reaction or create new 
     */
    onReact(type, isRemove) {
        this.props.reactPost({
            post: this.props.post._id,
            type: type,
            isRemove: isRemove
        });
    }

    toggleShowComments() {
        this.setState({
            isShowCommentList: !this.state.isShowCommentList
        })
    }

    onDownload() {
        this.setState({ isLoadingDownload: true })

        this.props.downloadFile(this.props.post).then(() => {
            this.setState({ isLoadingDownload: false })
        }).catch(err => {
            this.setState({ isLoadingDownload: false })
            notification.error({
                message: ' c li xy ra',
                description: 'Vui lng th li'
            })
        })
    }

    onBookmark() {
        this.props.createBookmark({ post_id: this.props.post._id }).then(() => {
            notification.success({
                message: ' lu bi vit thnh cng'
            })
        }).catch(err => {
            console.log(err)
            notification.error({
                message: ' c li xy ra',
                description: 'Vui lng th li'
            })
        })
    }

    toggleReportModal(visible) {
        this.setState({ isOpenReportModal: visible })
    }

    onChangeReportValue(e) {
        this.setState({ reportValue: e.target.value });
    }

    onReport() {
        confirm({
            title: 'Cnh bo',
            content: 'Bn chc chn mun bo co bi vit ny ch?',
            cancelText: 'Hu',
            okText: 'OK',
            onOk: () => {
                this.props.reportPost({
                    post_id: this.props.post._id,
                    reason: this.state.reportValue
                }).then(() => {
                    this.toggleReportModal(false);
                    notification.success({
                        message: ' bo co bi vit thnh cng'
                    })
                }).catch(err => {
                    notification.error({
                        message: ' c li xy ra',
                        description: 'Vui lng th li'
                    })
                })
            }
        })
    }

    toggleUpdateModal(visible) {
        this.setState({ isOpenUpdateModal: visible, postContent: this.props.post.content })
    }

    onChangePostContent(value) {
        this.setState({ postContent: value });
    }

    onUpdate() {
        confirm({
            title: 'Cnh bo',
            content: 'Bn chc chn mun cp nht bi vit ny ch?',
            cancelText: 'Hu',
            okText: 'OK',
            onOk: () => {
                this.props.updatePostDetail(this.props.post._id, {
                    content: this.state.postContent
                }).then(() => {
                    this.toggleUpdateModal(false);
                    notification.success({
                        message: ' cp nht bi vit thnh cng'
                    })
                }).catch(err => {
                    notification.error({
                        message: ' c li xy ra',
                        description: 'Vui lng th li'
                    })
                })
            }
        })
    }

    onDelete() {
        confirm({
            title: 'Cnh bo',
            content: 'Bn chc chn mun xo bi vit ny ch?',
            cancelText: 'Hu',
            okText: 'OK',
            onOk: () => {
                this.props.deletePost(this.props.post._id).then(() => {
                    notification.success({
                        message: ' xo bi vit thnh cng'
                    })
                }).catch(err => {
                    notification.error({
                        message: ' c li xy ra',
                        description: 'Vui lng th li'
                    })
                })
            }
        })
    }

    render() {
        var { user, post, reaction, likeNumber, dislikeNumber, downloadNumber } = this.props;
        var { isShowCommentList, isLoadingDownload, isOpenReportModal, isOpenUpdateModal, reportValue, postContent } = this.state;

        var actionButtons = [
            <ReactionIcon type={reaction || 'like'} reacted={reaction} onReact={(type, isRemove) => this.onReact(type, isRemove)} />,
            <div className="d-flex align-items-center justify-content-center" onClick={() => this.toggleShowComments()}>
                <MessageOutlined key="comments" className="custom-card-actions" />
                <span className="ml-2 custom-card-actions-text">Bnh lun</span>
            </div>,
            <div className="d-flex align-items-center justify-content-center" >
                <FacebookShareButton url={`${appConfig.ROOT_URL}/posts/${post._id}`}>
                    <ShareAltOutlined key="share" className="custom-card-actions" />
                    <span className="ml-2 custom-card-actions-text">Chia s</span>
                </FacebookShareButton>
            </div>
        ];

        if (post.file_id) {
            actionButtons.push(
                <div className="d-flex align-items-center justify-content-center" onClick={() => this.onDownload()}>
                    {isLoadingDownload ? <LoadingOutlined key="download" className="custom-card-actions" /> : <CloudDownloadOutlined key="download" className="custom-card-actions" />}
                    <span className="ml-2 custom-card-actions-text">Ti v</span>
                </div>
            )
        }

        if (!user) {
            actionButtons = [
                <div className="d-flex align-items-center justify-content-center" onClick={() => this.toggleShowComments()}>
                    <MessageOutlined key="comments" className="custom-card-actions" />
                    <span className="ml-2 custom-card-actions-text">Bnh lun</span>
                </div>,
                <div className="d-flex align-items-center justify-content-center" >
                    <FacebookShareButton url={`${appConfig.ROOT_URL}/posts/${post._id}`}>
                        <ShareAltOutlined key="share" className="custom-card-actions" />
                        <span className="ml-2 custom-card-actions-text">Chia s</span>
                    </FacebookShareButton>
                </div>
            ]
        }

        return (
            <div className="single-post">
                <Card
                    actions={actionButtons}
                >
                    <div className="d-flex justify-content-between align-items-lg-start">
                        <Meta
                            avatar={
                                <Link to={`/users/${post.creator._id}`}>
                                    <Avatar src={post.creator.avatar} size={47} />
                                </Link>
                            }
                            title={
                                <Link className="text-none" to={`/users/${post.creator._id}`}>
                                    <Typography.Text strong>{post.creator.name}</Typography.Text>
                                </Link>
                            }
                            description={
                                <Link className="text-secondary" to={`/posts/${post._id}`}>
                                    <small><ClockCircleOutlined /> {processTime(post.updated_at)}</small>
                                </Link>
                            }
                        />
                        {
                            user ? (
                                <div>
                                    <Popover
                                        placement="bottom"
                                        content={
                                            <List
                                                size="small"
                                                header={null}
                                                footer={null}
                                                bordered={false}
                                                dataSource={this.postMoreActions}
                                                renderItem={item => (
                                                    <List.Item onClick={item.action} className="item-action-btn">{item.icon} {item.name}</List.Item>
                                                )}
                                            />
                                        }
                                        title={null}
                                    >
                                        <EllipsisOutlined className="post-more-action-btn" />
                                    </Popover>
                                </div>
                            ) : null
                        }
                    </div>
                    <div className="mt-3">
                        {ReactHtmlParser(post.content)}
                    </div>
                    {
                        post.file_id ? (
                            <Card className="mt-3">
                                <div className="d-flex justify-content-start align-items-center">
                                    <FileProtectOutlined style={{ fontSize: '40px' }} />
                                    <div className="ml-4">
                                        <h5>{post.file_name}</h5>
                                        <small></small>
                                    </div>
                                </div>
                            </Card>
                        ) : null
                    }
                    {
                        post.images.length ? (
                            <div className="post-images">
                                <FbImageLibrary
                                    images={post.images}
                                    countFrom={3}
                                    renderOverlay={() => "Xem nh"}
                                />
                            </div>
                        ) : null
                    }
                    <div className="d-flex justify-content-between align-items-center mt-3">
                        <div className="d-flex justify-content-start align-items-center">
                            <LikeTwoTone twoToneColor="#3498db" />
                            <span className="ml-1">{likeNumber}</span>
                            <Divider type="vertical" />
                            <DislikeTwoTone twoToneColor="#c0392b" />
                            <span className="ml-1">{dislikeNumber}</span>
                        </div>
                        <div>
                            {downloadNumber ? `${downloadNumber} lt ti v` : null}
                        </div>
                    </div>
                </Card>
                {
                    isShowCommentList ? (<CommentList className="comments" post={post._id} />) : null
                }

                {/* Report modal */}
                <Modal
                    visible={isOpenReportModal}
                    onOk={() => this.onReport()}
                    onCancel={() => this.toggleReportModal(false)}
                    title="Bo co bi vit"
                    okText="Gi bo co"
                    cancelText="Hu"
                >
                    <Radio.Group onChange={(e) => this.onChangeReportValue(e)} value={reportValue}>
                        <Radio style={radioStyle} value={'not_relevant'}>
                            Ni dung bi vit khng lin quan ti hc tp
                        </Radio>
                        <Radio style={radioStyle} value={'corrupt_file'}>
                            Khng th ti file
                        </Radio>
                        <Radio style={radioStyle} value={'spam'}>
                            Bi vit spam
                        </Radio>
                        <Radio style={radioStyle} value={'fake_news'}>
                            Thng tin sai lch
                        </Radio>
                    </Radio.Group>
                </Modal>
                {/* Update modal */}
                <Modal
                    visible={isOpenUpdateModal}
                    onOk={() => this.onUpdate()}
                    onCancel={() => this.toggleUpdateModal(false)}
                    title="Sa bi vit"
                    okText="Cp nht"
                    cancelText="Hu"
                >
                    <div className="form-post">
                        <ReactQuill
                            theme={"snow"}
                            placeholder="Bn mun chia s iu g?"
                            value={postContent}
                            onChange={(value) => this.onChangePostContent(value)}
                            modules={
                                {
                                    toolbar: [
                                        [{ 'header': [1, 2, 3, 4, false] }],
                                        ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                                        [{ 'list': 'ordered' }, { 'list': 'bullet' }],
                                        ['link']
                                    ],
                                }
                            }
                            formats={[
                                'header',
                                // {
                                //     name: "h1",
                                //     definition: { tag: "H1", prepare: "heading", type: "line" }
                                // },
                                // {
                                //     name: "h2",
                                //     definition: { tag: "H2", prepare: "heading", type: "line" }
                                // },
                                // {
                                //     name: "h3",
                                //     definition: { tag: "H3", prepare: "heading", type: "line" }
                                // }
                                'bold', 'italic', 'underline', 'strike', 'blockquote',
                                'list', 'bullet',
                                'link', 'image'
                            ]}
                        />
                    </div>
                </Modal>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser
    }
}

function mapDispatchToProps(dispatch) {
    return {
        reactPost: (data) => dispatch(reactPost(data)),
        downloadFile: (post) => dispatch(downloadFile(post)),
        updatePostDetail: (id, data) => dispatch(updatePostDetail(id, data)),
        reportPost: (data) => dispatch(reportPost(data)),
        deletePost: (id) => dispatch(deletePost(id)),
        createBookmark: (data) => dispatch(createBookmark(data)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Post);