import _ from 'lodash';
import {
    GET_ALL_NOTIFICATIONS,
    MARK_ALL_AS_READ,
    MARK_NOTIFICATION_AS_READ,
    COUNT_UNREAD
} from '../actions/types';

const INIT_STATE = {
    notifications: [],
    unread: 0,
    pagination: {
        current: 1,
        total: 0,
        totalPages: 0,
        perPage: 0
    }
};

export default (state = INIT_STATE, action) => {
    switch (action.type) {
        case GET_ALL_NOTIFICATIONS: {
            let notifications = [...action.payload.notifications];  // for first page
            if (action.payload.current > 1) {
                notifications = [...state.notifications, ...action.payload.notifications]
            }

            return {
                ...state,
                notifications: notifications,
                pagination: {
                    current: action.payload.current,
                    total: action.payload.total,
                    totalPages: action.payload.pages,
                    perPage: action.payload.perPage
                }
            };
        }
        case MARK_ALL_AS_READ: {
            let notifications = [...state.notifications];
            notifications = notifications.map(item => {
                item.is_read = true;
                return item;
            })

            return {
                ...state,
                unread: 0,
                notifications: notifications,
            }
        }
        case MARK_NOTIFICATION_AS_READ: {
            let notifications = [...state.notifications];
            let index = _.findIndex(notifications, (item) => {
                return item._id.toString() == action.payload.toString();
            });
            notifications[index].is_read = true;

            return {
                ...state,
                unread: state.unread - 1,
                notifications: notifications,
            }
        }
        case COUNT_UNREAD: {
            return { ...state, unread: action.payload }
        }
        default: {
            return { ...state };
        }
    }
}