import _ from 'lodash';
import {
    CREATE_POST,
    GET_ALL_POSTS,
    REACTION_POST,
    GET_POST_DETAIL,
    DOWNLOAD_FILE,
    UPDATE_POST_CONTENT,
    BOOKMARK_POST,
    REPORT_POST,
    DELETE_POST
} from '../actions/types';

const INIT_STATE = {
    posts: [],
    currentPost: null,
    pagination: {
        current: 1,
        total: 0,
        totalPages: 0,
        perPage: 0
    }
};

export default (state = INIT_STATE, action) => {
    switch (action.type) {
        case CREATE_POST: {
            let posts = [...state.posts];
            posts = [
                {
                    ...action.payload,
                    likes: 0,
                    dislikes: 0,
                    downloads: 0,
                    reaction: null
                },
                ...posts
            ]

            return {
                ...state,
                posts: posts
            };
        }
        case GET_ALL_POSTS: {
            let posts = [...action.payload.posts];  // for first page
            if (action.payload.current > 1) {
                posts = [...state.posts, ...action.payload.posts]
            }
            return {
                ...state,
                posts: posts,
                pagination: {
                    current: action.payload.current,
                    total: action.payload.total,
                    totalPages: action.payload.pages,
                    perPage: action.payload.perPage
                }
            };
        }
        case REACTION_POST: {
            let { reaction, isRemove } = action.payload;

            let currentPosts = [...state.posts];

            let index = _.findIndex(state.posts, (item) => {
                return item._id.toString() == reaction.post.toString();
            })

            if (isRemove) {
                if (reaction.type == 'like') currentPosts[index].likes -= 1;
                else currentPosts[index].dislikes -= 1;

                currentPosts[index].reaction = null;
            } else {
                if (reaction.type == 'like') {
                    currentPosts[index].likes += 1;
                    if (currentPosts[index].reaction == 'dislike') currentPosts[index].dislikes -= 1;
                } else {
                    currentPosts[index].dislikes += 1;
                    if (currentPosts[index].reaction == 'like') currentPosts[index].likes -= 1;
                }

                currentPosts[index].reaction = reaction.type; // update new reaction
            }

            if (state.currentPost) {
                return { ...state, posts: currentPosts, currentPost: currentPosts[index] };
            } else {
                return { ...state, posts: currentPosts };
            }
        }
        case GET_POST_DETAIL: {
            return {
                ...state,
                currentPost: action.payload.post
            };
        }
        case UPDATE_POST_CONTENT: {
            let updatedPost = action.payload.post;
            let currentPosts = [...state.posts];

            let index = _.findIndex(state.posts, (item) => {
                return item._id.toString() == updatedPost._id.toString();
            })

            currentPosts[index] = { ...currentPosts[index], ...updatedPost };

            if (state.currentPost) {
                return { ...state, posts: currentPosts, currentPost: currentPosts[index] };
            } else {
                return { ...state, posts: currentPosts };
            }
        }
        case DOWNLOAD_FILE: {
            let currentPosts = [...state.posts];

            let index = _.findIndex(state.posts, (item) => {
                return item._id.toString() == action.payload.toString();
            })

            currentPosts[index].downloads += 1;

            if (state.currentPost) {
                return { ...state, posts: currentPosts, currentPost: currentPosts[index] };
            } else {
                return { ...state, posts: currentPosts };
            }
        }
        case BOOKMARK_POST: {
            return { ...state };
        }
        case REPORT_POST: {
            return { ...state };
        }
        case DELETE_POST: {
            let currentPosts = [...state.posts];

            let posts = currentPosts.filter((item) => {
                return item._id.toString() != action.payload.toString();
            })

            if (state.currentPost._id.toString() == action.payload.toString()) {
                return { ...state, posts: posts, currentPost: null };
            } else {
                return { ...state, posts: posts };
            }
        }
        default: {
            return { ...state };
        }
    }
}