import React, { Component } from 'react';
import { connect } from 'react-redux';
import AppHeader from './AppHeader';
import AppSidebar from './AppSidebar';
import AppFooter from './AppFooter';
import { Layout, BackTop } from 'antd';
import DynamicAntdTheme from 'dynamic-antd-theme';
// actions
import { getAuthUser } from '../redux/actions/UserActions';

const { Content } = Layout;

class AppLayout extends Component {
    async componentDidMount() {
        await this.props.getAuthUser();
    }

    render() {
        var { user } = this.props;

        return (
            <div>
                <Layout>
                    <AppHeader></AppHeader>
                    <Content id="main-content-wrapper" >
                        <Layout>
                            <AppSidebar user={user}></AppSidebar>
                            <Content id="main-content">
                                {this.props.children}
                            </Content>
                        </Layout>
                    </Content>
                    <AppFooter></AppFooter>


                </Layout>
                <div style={{ visibility: 'hidden' }}>
                    <DynamicAntdTheme primaryColor='#dd1818' />
                </div>
                <BackTop />
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getAuthUser: () => dispatch(getAuthUser())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppLayout);