import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { List, Avatar, Button, Pagination, Card, Empty, Typography, Modal, notification, Spin, Divider } from 'antd';
import { cleanObject, processTime } from '../../utils/helpers';
import Truncate from 'react-truncate';
import qs from 'qs';
import {
    isBrowser,
    isMobile
} from "react-device-detect";
import InfiniteScroll from "react-infinite-scroll-component";
// actions
import { getListLiked } from '../../redux/actions/HistoryActions';

const Item = ({ item, ...props }) => (
    <List.Item {...props}>
        <List.Item.Meta
            avatar={
                <Avatar src={item.creator.avatar} />
            }
            title={
                <Link className="text-none" to={`/users/${item.creator._id}`}>{item.creator.name}</Link>
            }
            description={
                <div>
                    <Truncate lines={2} ellipsis={<span>...</span>}>
                        {item.content}
                    </Truncate>
                    <div><i> Bi vit c to lc {processTime(item.created_at)}</i></div>
                    <Link to={`/posts/${item._id}`}>
                        <Button type="link" className="p-0">Xem bi vit</Button>
                    </Link>
                </div>
            }
        />
    </List.Item>
)

class LikedPosts extends Component {
    state = {
        isLoading: true
    }

    componentDidMount() {
        let query = qs.parse(this.props.location.search.slice(1));
        this.props.getListLiked(query).then(() => {
            this.setState({ isLoading: false })
        })
    }

    // use for mobile only
    fetchMorePosts() {
        this.props.getListLiked({ page: this.props.pagination.current + 1 })
    }

    // use for desktop only
    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.location.search !== this.props.location.search) {
            var query = qs.parse(nextProps.location.search.slice(1));
            this.setState({ isLoading: true });
            this.props.getListLiked(query).then(() => {
                setTimeout(() => {
                    this.setState({
                        isLoading: false
                    })
                }, 800)
            });
        }
    }

    onChangeFilter(name, value) {
        this.setState({ isLoading: true });
        let query = qs.parse(this.props.location.search.slice(1));
        query = {
            ...query,
            [name]: value
        }
        cleanObject(query);

        this.props.history.push({
            pathname: '/history/liked',
            search: qs.stringify(query)
        });
    }

    render() {
        var { user, posts, pagination } = this.props;
        var { isLoading } = this.state;

        if (isLoading) {
            return (
                <div className="text-center">
                    <Spin size="large" />
                </div>
            )
        }

        return (
            <div>
                {
                    posts.length ? (
                        <Card>
                            <Typography.Title level={2}>Bi vit  thch</Typography.Title>
                            {
                                isBrowser ? (
                                    <div>
                                        <List
                                            itemLayout="horizontal"
                                            dataSource={posts}
                                            renderItem={item => (
                                                <Item item={item} key={item._id} />
                                            )}
                                        />

                                        <div className="text-right mt-4">
                                            <Pagination
                                                {...pagination}
                                                pageSize={pagination.perPage}
                                                onChange={(page, pageSize) => this.onChangeFilter('page', page)}
                                            />
                                        </div>
                                    </div>
                                ) : null
                            }
                            {
                                isMobile ? (
                                    <InfiniteScroll
                                        className="notification-list"
                                        dataLength={pagination.perPage * pagination.current}
                                        next={() => this.fetchMorePosts()}
                                        hasMore={pagination.current < pagination.totalPages}
                                        loader={<div className="text-center"><Spin size="large" /></div>}
                                    >
                                        {
                                            posts.map(item => (
                                                <Item item={item} key={item._id} />
                                            ))
                                        }
                                    </InfiniteScroll>
                                ) : null
                            }
                        </Card>
                    ) : (
                            <Card>
                                <Typography.Title level={2}>Bi vit  thch</Typography.Title>
                                <Empty description="Khng c d liu" />
                            </Card>
                        )
                }
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user.authUser,
        posts: state.history.liked.posts,
        pagination: state.history.liked.pagination,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getListLiked: (query) => dispatch(getListLiked(query)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LikedPosts);