import api from '../../utils/api';
import {
    CREATE_BOOKMARK,
    GET_ALL_BOOKMARKS,
    DELETE_BOOKMARK
} from './types';

export const createBookmark = (data) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.post('/bookmarks', data).then(res => {
            dispatch({ type: CREATE_BOOKMARK, payload: res.data });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const getAllBookmarks = (query) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.get('/bookmarks', {params: query}).then(res => {
            dispatch({ type: GET_ALL_BOOKMARKS, payload: res.data });
            resolve(res.data)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}

export const deleteBookmark = (id) => dispatch => {
    return new Promise((resolve, reject) => {
        return api.delete(`/bookmarks/${id}`).then(res => {
            dispatch({ type: DELETE_BOOKMARK, payload: id });
            resolve(true)
        }).catch(err => {
            console.error(err.response);
            reject(err);
        })
    })
}