import moment from 'moment';

export function processTime(timestamp) {
    let now = moment();
    
    let duration = moment.duration(now.diff(moment(timestamp)));
    let hours = Math.ceil(duration.asHours());
    let minutes = Math.ceil(duration.asMinutes());

    let date = moment(timestamp).format('HH:mm DD/MM/YYYY');
    if (hours <= 23) date = `${hours} gi trc`;
    if (minutes < 60) date = `${minutes} pht trc`;
    if (minutes < 2) date = `Va xong`;

    return date;
}

export function cleanObject (obj) {
    for (var propName in obj) {
        if (!propName || !obj[propName]) {
            delete obj[propName];
        }
    }
    return obj;
}