import _ from 'lodash';
import {
    GET_LIST_LIKED,
    GET_LIST_DOWNLOADED
} from '../actions/types';
import {
    isBrowser,
    isMobile
} from "react-device-detect";

const INIT_STATE = {
    liked: {
        posts: [],
        pagination: {
            current: 1,
            total: 0,
            totalPages: 0,
            perPage: 0
        }
    },
    downloaded: {
        posts: [],
        pagination: {
            current: 1,
            total: 0,
            totalPages: 0,
            perPage: 0
        }
    }
};

export default (state = INIT_STATE, action) => {
    switch (action.type) {
        case GET_LIST_LIKED: {
            let posts = [...action.payload.posts];

            if(isMobile) {
                if(action.payload.current > 1) posts = [...state.liked.posts, ...action.payload.posts]
            }
            
            return {
                ...state,
                liked: {
                    posts: posts,
                    pagination: {
                        current: action.payload.current,
                        total: action.payload.total,
                        totalPages: action.payload.pages,
                        perPage: action.payload.perPage
                    }
                }
            };
        }
        case GET_LIST_DOWNLOADED: {
            let posts = [...action.payload.posts];

            if(isMobile) {
                if(action.payload.current > 1) posts = [...state.downloaded.posts, ...action.payload.posts]
            }
            
            return {
                ...state,
                downloaded: {
                    posts: posts,
                    pagination: {
                        current: action.payload.current,
                        total: action.payload.total,
                        totalPages: action.payload.pages,
                        perPage: action.payload.perPage
                    }
                }
            };
        }
        default: {
            return { ...state };
        }
    }
}