import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Comment, Tooltip } from 'antd';
import moment from 'moment';
import ReactHtmlParser from 'react-html-parser';
import { processTime } from '../../utils/helpers';
import { DislikeOutlined, LikeOutlined, DislikeFilled, LikeFilled } from '@ant-design/icons';
// actions
import { reactComment } from '../../redux/actions/CommentActions';

class CommentItem extends Component {
    state = {
        likes: 0,
        dislikes: 0,
        type: null,
        isShowReplies: false,
        currentReplies: 0, // current replies number, use this variable for update propose 
    }

    componentDidMount() {
        let { comment } = this.props;
        this.setState({
            likes: comment.likes,
            dislikes: comment.dislikes,
            reaction: comment.reaction,
            currentReplies: comment.child_comments.length,
            isShowReplies: false || this.props.isShowAllComments
        })
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        this.setState({isShowReplies: nextProps.isShowAllComments})
    }

    onReactComment(type) {
        let isRemove = false;
        let { likes, dislikes, reaction } = this.state;

        if (reaction == type) isRemove = true;

        this.props.reactComment({
            comment: this.props.comment._id,
            type: type,
            isRemove: isRemove
        });

        if (isRemove) {
            // reset
            this.setState({
                reaction: null,
                likes: type == 'like' ? likes - 1 : likes,
                dislikes: type == 'dislike' ? dislikes - 1 : dislikes,
            })
        } else {
            // change to rest reaction
            if (reaction) {
                this.setState({
                    reaction: type,
                    likes: type == 'like' ? likes + 1 : likes - 1,
                    dislikes: type == 'dislike' ? dislikes + 1 : dislikes - 1,
                })
            } else {
                this.setState({
                    reaction: type,
                    likes: type == 'like' ? likes + 1 : likes,
                    dislikes: type == 'dislike' ? dislikes + 1 : dislikes,
                })
            }

        }
    }

    onReply() {
        this.toggleShowReplies(true);
        this.props.onReply();
    }

    toggleShowReplies() {
        this.setState({isShowReplies: !this.state.isShowReplies})
    }

    render() {
        var { comment, children } = this.props;
        var { likes, dislikes, reaction, isShowReplies } = this.state;

        return (
            <Comment
                actions={[
                    <span key="comment-basic-like" onClick={() => this.onReactComment('like')}>
                        <Tooltip title="Thch">
                            {
                                reaction == 'like' ? <LikeFilled style={{ color: "#3498db" }} /> : <LikeOutlined />
                            }
                        </Tooltip>
                        <span className="comment-action">{likes}</span>
                    </span>,
                    <span key="comment-basic-dislike" onClick={() => this.onReactComment('dislike')}>
                        <Tooltip title="Khng thch">
                            {
                                reaction == 'dislike' ? <DislikeFilled style={{ color: "#c0392b" }} /> : <DislikeOutlined />
                            }
                        </Tooltip>
                        <span className="comment-action">{dislikes}</span>
                    </span>,
                    <span key="comment-basic-reply-to" onClick={() => this.onReply()}>Tr li</span>,
                    <span key="comment-basic-child" onClick={() => this.toggleShowReplies()}>
                        {comment.child_comments.length ? (<span>{isShowReplies ? "Thu gn" : `Xem ${comment.child_comments.length} tr li`}</span>) : null}
                    </span>,
                ]}
                author={comment.user.name}
                avatar={comment.user.avatar}
                content={ReactHtmlParser(comment.content)}
                datetime={processTime(comment.created_at)}
            >
                {
                    isShowReplies ? children : null
                }
            </Comment>
        )
    }
}

function mapStateToProps(state) {
    return {

    }
}

function mapDispatchToProps(dispatch) {
    return {
        reactComment: (data) => dispatch(reactComment(data)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CommentItem);