import React, { Component } from 'react';
import Icon, {
    LikeOutlined,
    DislikeOutlined,
    LikeTwoTone,
    DislikeTwoTone,
    LikeFilled,
    DislikeFilled
} from '@ant-design/icons';
import { Card, Avatar, Typography, Button, Divider, Popover } from 'antd';
import { ShakeVertical } from 'reshake'

export default class ReactionIcon extends Component {
    state = {
        visible: false,
        type: 'like',
        reacted: false
    }

    componentDidMount() {
        this.setState({
            type: this.props.type,
            reacted: this.props.reacted,
        })
    }

    onClickReactionIcon = (type) => {
        let isRemove = false;
        if(this.state.type == type && this.state.reacted) isRemove = true;

        this.props.onReact(type, isRemove);
 
        if(isRemove) {
            // reset
            this.setState({ 
                visible: false,
                type: 'like',
                reacted: false
            })
        } else {
            // change to rest reaction
            this.setState({ 
                visible: false,
                type: type,
                reacted: true
            })
        }
    }

    handleVisibleChange = visible => {
        this.setState({ visible });
    };

    render() {
        var { visible, type, reacted } = this.state;

        // console.log(type)

        let icon = null;
        let text = "Thch";
        let textColor = null;

        if (type == 'like') {
            if (reacted) {
                icon = <LikeFilled style={{color: "#3498db"}} className="custom-card-actions" />;
                textColor = "#3498db";
            }
            else icon = <LikeOutlined className="custom-card-actions" />
        } else if (type == 'dislike') {
            text = "Khng thch";
            if (reacted) {
                icon = <DislikeFilled style={{color: "#c0392b"}} className="custom-card-actions" />;
                textColor = "#c0392b";
            }
            else icon = <DislikeOutlined className="custom-card-actions" />
        }

        return (
            <Popover
                trigger={['hover', 'focus', 'click']}
                visible={visible}
                content={
                    <div className="d-flex">
                        <ShakeVertical dur={1000} v={8}>
                            <Button type="primary" shape="circle" size="small"  onClick={() => this.onClickReactionIcon('like')}>
                                <LikeOutlined twoToneColor="#3498db" className="reaction-icons-1 like" />
                            </Button>
                        </ShakeVertical>
                        <Divider type="vertical" />
                        <ShakeVertical dur={1000} v={8}>
                            <Button type="primary" danger shape="circle" size="small"  onClick={() => this.onClickReactionIcon('dislike')}>
                                <DislikeOutlined twoToneColor="#c0392b" className="reaction-icons-1 dislike" />
                            </Button>
                        </ShakeVertical>
                    </div>
                }
                onVisibleChange={this.handleVisibleChange}
            >
                <div className="d-flex align-items-center justify-content-center" onClick={() => this.onClickReactionIcon(type)}>
                    {icon}
                    <span className="ml-2 custom-card-actions-text" style={{color: textColor}}>
                        {reacted ? <strong>{text}</strong> : text}
                    </span>
                </div>
            </Popover>
        )
    }
}
